function dateToName(date: Date) {
  return `${date.getFullYear()}-${String(101 + date.getMonth()).substring(1)}-${String(100 + date.getDate()).substring(1)}`;
}

function relativeDate(days: number) {
  const date = new Date();
  date.setDate(date.getDate() + days);
  return date;
}

export function resolveDateAlias(alias: string) {
  switch (alias.toLowerCase()) {
    case 'today':
      return dateToName(new Date());
    case 'tomorrow':
      return dateToName(relativeDate(1));
    case 'yesterday':
      return dateToName(relativeDate(-1));
    default:
      return;
  }
}